import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import '../../css/Overlay.css';
import Close from '../../assets/images/close.png';


class Overlay extends Component {
    constructor(props) {
        super(props);
        this.overlayContainer = document.createElement('div');
        this.overlayContainer.className = 'overlay-container d-flex justify-content-center align-items-center';
        document.body.appendChild(this.overlayContainer);
    }

    componentWillUnmount() {
        document.body.removeChild(this.overlayContainer);
        // this.setState({ animateInOut: 'fadeOutRightBig' })
    }

    // overlay card animated fadeInLeftBig
    render() {
        return ReactDOM.createPortal(
            <div className={ 'overlay card animated ' + this.props.animateInOut }>
                <div className="card-header">
                    <span onClick={ this.props.onClose } className="float-right">
                        <img src={ Close } alt="close button" className="img-fluid cose-btn" />
                    </span>
                </div>
                <div className="card-body">
                    { this.props.children }
                </div>
            </div>,
            this.overlayContainer
        );
    }
}

export default Overlay;



