import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactDOM from 'react-dom';
import Overlay from '../shared/Overlay';
import ErrorBoundary from '../shared/ErrorBoundary';
import { fetch_movie_most_popular, fetch_youtube_trailer, imageUrlBase, image_sizes } from '../../config/actions/movieActions';


class PopularView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isOverlayActive: false,
            animateInOut: 'fadeInLeftBig'
        };
    }

    closeOverlay() {
        this.setState({ animateInOut: 'fadeOutRightBig' })
        setTimeout(() => {
            this.setState({ isOverlayActive: false })
        }, 200);
    }

    openOverlay() {
        this.setState({
            isOverlayActive: true,
            animateInOut: 'fadeInLeftBig'
        })
    }

    // componentDidMount() {
    //     fetch_movie_most_popular();
    // }
    getExerpt(str) {
        let exerpt = str.split(' ').slice(0, 10);
        return exerpt.join(' ');
    }

    iframe(html) {
        return { __html: html }
    }

    startTrailer(movie_title) {
        // console.log('FASDFASDFASSDFASDFA');
        this.openOverlay();
        this.props.fetch_youtube_trailer(movie_title);

        // this.props.current_youtube_trailer_movie_id = id
    }

    render() {
        return (
            <ErrorBoundary place='Popular'>
                <div className="latest-movies">

                    { this.state.isOverlayActive &&
                        <Overlay
                            animateInOut={ this.state.animateInOut }
                            onClose={ () => this.closeOverlay() }
                        >

                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-sm-5">
                                        { this.props.most_popular_movies.map((movie, index) => {
                                            if (movie.title === this.props.current_youtube_trailer_movie_title) return (
                                                <div className="overlay-trailer-infos">
                                                    <img key={ index + 1 } src={ imageUrlBase + image_sizes[4] + movie.backdrop_path } alt="poster image" className="rounded poster-image img-fluid" />
                                                    <h3 className="my-3">{ movie.title }</h3>
                                                    <p className="">{ movie.overview }</p>
                                                </div>
                                            )
                                        }) }

                                    </div>
                                    <div className="col-sm-7">
                                        <div dangerouslySetInnerHTML={ this.iframe(`<iframe style="width:45vw;height:25vw" src="https://www.youtube.com/embed/${this.props.current_youtube_trailer_movie_id}" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe>`) } />
                                    </div>
                                </div>
                            </div>

                        </Overlay>
                    }

                    <h3 className="text-center mt-2 mb-4">Popular Movies..</h3>
                    <div className="row">
                        { this.props.most_popular_movies.map((movie, index) => {
                            return (
                                <div key={ index.toString() } className="col-sm-12 col-md-6 col-lg-4 my-3">
                                    <div className="container-fluid d-flex">

                                        <div className="img-container">
                                            <img src={ imageUrlBase + image_sizes[1] + movie.poster_path } alt="poster image" className="rounded poster-image" />
                                        </div>

                                        <div className="movie-info px-3 d-flex flex-column justify-content-around">
                                            <h2 className="movie--title">{ movie.title }</h2>
                                            <div className="description">
                                                <p>{ this.getExerpt(movie.overview) }  <a href="">[...continue]</a> </p>
                                            </div>
                                            <div className="btn-group btn-group-sm mr-auto" role="group" aria-label="Basic example">
                                                <button
                                                    type="button"
                                                    className="btn btn-sm btn-warning"
                                                    onClick={ () => this.startTrailer(movie.title) }>Watch Trailer</button>
                                                {/* <button type="button" className="btn btn-sm btn-info">Read More</button> */ }
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            )
                        }) }
                    </div>
                </div>
            </ErrorBoundary>
        );
    }
}

const mapStateToProps = state => {
    return {
        most_popular_movies: state.movies.most_popular_movies,
        current_youtube_trailer_movie_id: state.movies.current_youtube_trailer_movie_id,
        current_youtube_trailer_movie_title: state.movies.current_youtube_trailer_movie_title
    }
}

export default connect(mapStateToProps, { fetch_movie_most_popular, fetch_youtube_trailer })(PopularView);


